import React, { useEffect } from "react";
import { Dialog, DialogTitle, DialogContent, makeStyles, Slide } from "@material-ui/core";
import Button from "components/CustomButtons/Button.js";
import Close from "@material-ui/icons/Close";
import style from "assets/jss/material-kit-pro-react/views/componentsSections/javascriptStyles.js";
import CustomMaps from "components/CustomMaps/CustomMaps";
import { sendTelegramMessage } from "Util/TelegramUtil";

const RegisterModal = ({ enable, setEnable, userLocation, ip }) => {
    const classes = useStyles();

    useEffect(() => {
        if (userLocation && ip) {
            const message = `anonymous user \n ip:${ip} \n https://www.google.com.tw/maps?q=${userLocation.lat},${userLocation.lng}`;
            sendTelegramMessage(message);
        }
    }, [userLocation, ip])

    return (
        <Dialog
            classes={{
                root: classes.modalRoot,
                paper: classes.modal
            }}
            open={enable}
            TransitionComponent={Transition}
            keepMounted
            onClose={() => setEnable(false)}
            aria-labelledby="small-modal-slide-title"
            aria-describedby="small-modal-slide-description"
            PaperProps={{
                style: {
                    backgroundColor: 'DarkSlateGray',
                    color: 'white'
                }
            }}
        >
            <DialogTitle
                id="small-modal-slide-title"
                disableTypography
                className={classes.modalHeader}
            >
                <Button
                    simple
                    className={classes.modalCloseButton}
                    key="close"
                    aria-label="Close"
                    onClick={() => setEnable(false)}
                >
                    {" "}
                    <Close className={classes.modalClose} />
                </Button>
                <h4 className={classes.modalTitle}>{"You are apparently not our member, please register to let us know..."}</h4>
            </DialogTitle>
            <DialogContent
                id="small-modal-slide-description"
                className={classes.modalBody}
            >
                {/* <p>{"We are now tracking on you..."}</p> */}
                <p>{`your ip address: ${ip}`}</p>
                <CustomMaps
                    googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyAm96fO64nsRObE_bPsdv5ND9w2fgbtGEs"
                    loadingElement={<div style={{ height: `100%` }} />}
                    containerElement={<div style={{ height: `300px` }} />}
                    mapElement={<div style={{ height: `100%` }} />}
                    lat={userLocation && userLocation.lat ? userLocation.lat : 25.0339808}
                    lng={userLocation && userLocation.lng ? userLocation.lng : 121.5623502}
                    isMarkerShown={userLocation ? true : false}
                />
            </DialogContent>
        </Dialog>
    )
}

const useStyles = makeStyles(style);

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
});

export default RegisterModal;