/*eslint-disable*/
import React, { useState, useEffect, useRef } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import InputAdornment from "@material-ui/core/InputAdornment";
import Icon from "@material-ui/core/Icon";
// @material-ui/icons
import Face from "@material-ui/icons/Face";
// core components
import Footer from "components/Footer/Footer.js";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import image from "assets/img/bg52.jpg";
import FloatyAssistant from "components/Assistant/floaty";
import loginPageStyle from "assets/jss/material-kit-pro-react/views/loginPageStyle.js";
import { reduxDispatch } from "redux/store";
import * as types from "redux/module/loginModule/action";
import * as paths from "routes/constUrl";
import RegisterModal from "components/DialogModal/RegisterModal";
import { currentLocation, watchCurrentLocation, clearWatchPosition } from "Util/locationUtil";

const useStyles = makeStyles(loginPageStyle);

const DoormanPage = ({ history }) => {
    React.useEffect(() => {
        window.scrollTo(0, 0);
        document.body.scrollTop = 0;
    });

    const classes = useStyles();
    const inputEl = useRef(null);
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [inputValue, setInputValue] = useState("");
    const [isPasswordCorrect, setIsPasswordCorrect] = useState(false);

    //Register Modal
    const [enableRegisterModal, setEnableRegisterModal] = useState(false);
    const [userLocation, setUserLocation] = useState(undefined);
    const [errorMessage, setErrorMessage] = useState(undefined);
    const [ip, setIp] = useState(undefined);

    //驗證密碼
    useEffect(() => {
        if (isPasswordCorrect) {
            reduxDispatch(types.PASS_INDEX_PAGE, true);
            history.push(paths.LoginPageUrl);
        }
    }, [isPasswordCorrect])

    //取得 IP
    useEffect(() => {
        fetch('https://www.cloudflare.com/cdn-cgi/trace')
            .then(function (response) {
                return response.text();
            })
            .then(function (result) {
                const [f1, h, ipInfo] = result.split("\n");
                if (ipInfo) {
                    const [ip, value] = ipInfo.split("=");
                    setIp(value);
                }
            });
    }, [])

    //取得 GPS 座標
    useEffect(() => {
        setEnableRegisterModal(true);
        currentLocation(setUserLocation, setErrorMessage);
        const id = watchCurrentLocation(setUserLocation, setErrorMessage);

        return () => {
            if (id) {
                clearWatchPosition(id);
            }
        };
    }, [])

    //若取得座標失敗則轉導至錯誤畫面
    useEffect(() => {
        if (errorMessage) {
            console.log(errorMessage);
            history.push(paths.ErrorPageUrl);
        }
    }, [errorMessage])

    const onInputFocus = () => {
        inputEl.current.focus();

        // 開發測試階段跳過驗證
        if (process.env.NODE_ENV === "development") {
            setIsPasswordCorrect(true);
        }
    }

    const onInputChange = (e) => {
        const { value } = e.target;
        const iv = value.trim().toLowerCase();

        if (iv === "k") {
            setInputValue(value);
            setIsPasswordCorrect(true);
        } else {
            setInputValue(value);
        }
    }

    const onSubmit = (e) => {
        e.preventDefault();
        // history.push(paths.ProductPageUrl);
    }

    return (
        <div>
            <div
                className={classes.pageHeader}
                style={{
                    backgroundImage: "url(" + image + ")",
                    backgroundSize: "cover",
                    backgroundPosition: "top center"
                }}
            >
                <div className={classes.container}>
                    <GridContainer justify="center">
                        <GridItem xs={12} sm={12} md={4}>
                            <Card style={chatContainerBGColor}>
                                <form className={classes.form} onSubmit={onSubmit}>
                                    <CardBody signup>
                                        <CustomInput
                                            id="first"
                                            white
                                            formControlProps={{
                                                fullWidth: true
                                            }}
                                            inputProps={{
                                                placeholder: "Name...",
                                                type: "password",
                                                startAdornment: (
                                                    <InputAdornment position="start">
                                                        <Face className={classes.inputIconsColor} />
                                                    </InputAdornment>
                                                ),
                                                onChange: (e) => { setEmail(e.target.value) }
                                            }}
                                        />
                                        <CustomInput
                                            id="pass"
                                            white
                                            formControlProps={{
                                                fullWidth: true
                                            }}
                                            inputProps={{
                                                placeholder: "Password",
                                                type: "password",
                                                startAdornment: (
                                                    <InputAdornment position="start">
                                                        <Icon className={classes.inputIconsColor}>
                                                            lock_utline
                                                        </Icon>
                                                    </InputAdornment>
                                                ),
                                                onChange: (e) => { setPassword(e.target.value) }
                                            }}
                                        />
                                    </CardBody>
                                    <div className={classes.textCenter}>
                                        <Button type="submit" simple color="primary" color="info" size="lg">
                                            Get started
                                        </Button>
                                    </div>
                                </form>
                            </Card>
                        </GridItem>
                    </GridContainer>
                </div>
                <Footer
                    className={classes.footer}
                    content={
                        <div>
                            <div className={classes.right} onClick={onInputFocus}>
                                Copyright  &copy; {1900 + new Date().getYear()} {" "}
                                KLK Coffee
                                <input style={{ opacity: 0, display: 'inline', width: '0px' }}
                                    ref={inputEl}
                                    onChange={onInputChange}
                                    value={inputValue}
                                ></input>
                            </div>
                        </div>
                    }
                />
                <RegisterModal
                    enable={enableRegisterModal}
                    setEnable={setEnableRegisterModal}
                    userLocation={userLocation}
                    ip={ip}
                />
                <FloatyAssistant></FloatyAssistant>
            </div>
        </div>
    );
}

// 變更預設登入畫面, 輸入帳密位置的背景顏色
const chatContainerBGColor = {
    backgroundColor: 'dimgray'
}

export default DoormanPage;